import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Home from './Home'
class View extends Component {
  const
  render() {
    const viewStyle = {
      marginTop:70
    }
    return (
        <main style={viewStyle}>
          <Route exact path="/" component={Home} />
        </main>
    );
  }
}

export default View;
